export default function checkHeaderMenuItems() {
  const headerMenu = document.querySelector('.js-header-menu');
  if (!headerMenu) return;

  const menuContainer = headerMenu.querySelector('.header-menu__list');
  const burgerList = headerMenu.querySelector('.header-menu__burger-list');
  const burgerItems = burgerList.childNodes;

  // for (let i = burgerItems.length - 1; i >= 0; i--) {
  //   const item = burgerItems[i];
  //   if (item.classList && item.classList.contains('header-menu__item')) {
  //     menuContainer.append(item);
  //   }
  // }

  const menuItems = menuContainer.childNodes;

  menuItems.forEach(item => {
    if (item.classList && item.classList.contains('header-menu__item') && item.offsetTop > 0) {
      burgerList.append(item);
    }
  });

  menuContainer.removeAttribute('style');
}