export default function modals() {
    const instance = new HystModal({
        linkAttributeName: 'data-modal',
        closeOnOverlay: true,

        beforeOpen: function (modal) {
            window.closeMenu();
            const values = modal.starter.dataset;
            const form = modal.openedWindow.querySelector('form');

            if (form && values) {
                for (let key in values) {
                    if (key != "modal") {
                        const input = document.createElement('input');
                        input.type = "hidden";
                        input.classList.add('js-dynamic-input');
                        input.name = key;
                        input.value = values[key];
                        form.prepend(input);
                    }
                }
            }
        },
        afterClose: function (modal) {
            const form = modal.openedWindow.querySelector('form');
            if (form) {
                const inputs = form.querySelectorAll('.js-dynamic-input');
                inputs.forEach(input => input.remove());
            }
        },
    });

    window.modals = instance;
}